//OBJECT.CREATE - —Å–æ–∑–¥–∞–Ω–∏–µ –æ–±—ä–∫—Ç–∞

const person2 = Object.create({}, {
    name: {
        value: 'Ihar',
        enumerable: true, //–º–æ–¥–∏—Ñ–∏–∫–∞—Ç–æ—Ä enumerable –ø–æ –¥–µ—Ñ–æ–ª—Ç—É false
        writable: true, //–º–æ–¥–∏—Ñ–∏–∫–∞—Ç–æ—Ä writable –ø–æ –¥–µ—Ñ–æ–ª—Ç—É false
        configurable: true, 


    },
    lastName: {
        value: 'Maslakou',
        numerable: true, 
        writable: true, 
        configurable: true,
    },

    fullName: {
        get() { //—Ñ—É–Ω–∫—Ü–∏—è –≥–µ—Ç
            return this.name + ' ' + this.lastName; //—Å–ª–æ–∂–µ–Ω–∏–µ —Å—Ç—Ä–æ–∫
        },
        set(value) { //—Ñ—É–Ω–∫—Ü–∏—è —Å–µ—Ç
            this.name = value;
            this.lastName = value;
        }
    }
})

person2.fullName = 'Victor'; //"Victor Victor" –ø–æ—Å–ª–µ —Ñ—É–Ω–∫—Ü–∏–∏ set

for (let key in person2) {

    console.log('Key: ', key);

}



//__PROTO__
const animal = {
    eat: function() {
        console.log('Eat')
    }, 
    tail: true,
}

const cat = {
    tail: true,
}

const rabbit = {
    jump: function() {
        console.log('jump')
    },
}

cat.__proto__ = animal;
rabbit.__proto__ = animal;

console.log(rabbit);


//CLASS
class Animal { //—Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º –≤—ã–∑–æ–≤–∞ –∫–ª–∞—Å—Å–∞ —è–≤–ª—è–µ—Ç—Å—è –æ–±—ä–µ–∫—Ç
    constructor(options) { //–∫–ª–∞—Å—Å –≤—ã–∑—ã–≤–∞–µ—Ç –º–µ—Ç–æ–¥ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
        this.type = options.type;
        this.name = options.name;
        this.age = options.age;
    }

    sayHi() { //—É –∫–ª–≤—Å—Å–æ–≤ –º–æ–≥ –±—ã—Ç—å —Ñ-—Ü–∏–∏
        console.log('Hi');
    }
}

class Cat extends Animal {
    constructor(options) {
        super(options); //–≤—ã–∑–æ–≤ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–∞ Animal
        this.tail = true;
        this.name = options.name;
    }
}




// const animal2 = new Animal('rabbit', 'Bunny');
const catBiscuit = new Cat({
    name: 'Biscuit',
    age: 6
});

const catAnubis = new Cat({
    age: 5,
    name: 'Anubis'

})

console.log(catAnubis, catBiscuit); //Cat {type: undefined, name: "Anubis", age: 5, tail: true}
